<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <div class="not-allowed-wrap"  v-if="isShowNotAllowed"></div>
            <!---->
            <div class="common-btn-wrap">
                <el-button class="btn-cyan add-btn" @click="handleAdudit('multi')" :disabled="otherVariable.isSaving"></el-button>
                <el-button @click="handleRefresh"></el-button>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @selectChange="selectChange"
                @getData="getSearchList"
            >
                <template slot="search-other-btn">
                    <el-button @click="handleReset"></el-button>
                </template>
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @handleDetail="handleDetail"
                @handleSelectionChange="handleSelectionChange"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"

            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span v-if="!item.isCheck">{{ detailForm[item.prop] }}</span>
                                <span v-if="item.isCheck">{{ detailForm[item.prop] === 1 ? '':'' }}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap">
                    <div v-if="showButton" class="xs-audit-icon" @click="!otherVariable.isSaving && handleAdudit('alone')"  :class="otherVariable.isSaving ? 'banClick' : ''" ></div>
                    <div v-if="showUnAudit" class="xs-unaudit-icon" @click="handleUnAudit"></div>
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <div class="remark-wrap">
                        <div></div>
                        <div class="headspan scrollBar" :title="detailForm.remark">{{ detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <el-tabs v-model="activeName" class="common-content-tab order_style ">
                        <el-tab-pane label="" name="first">
                            <CommonTable
                                v-if="isResize"
                                v-loading="loadingI"
                                ref="tabTableI"
                                :isCenter="true"
                                :isMulTable="true"
                                :isNotMulSelect="false"
                                @handleOtherTable="handleOtherTable"
                                :otherVariable="otherVariable"
                                :formTableData="tabDataI"
                                :formPropTable="tabPropI"
                                :tableHeight="tableHeightI"
                            >
                            </CommonTable>
                        </el-tab-pane>
                    </el-tabs>
                    <div v-if="showTable" style="margin-top: 10px;">
                        <CommonTable
                            v-if="isResize"
                            ref="tabTableII"
                            :isCenter="true"
                            :isMulTable="true"
                            :isNotMulSelect="false"
                            :otherVariable="otherVariable"
                            :formTableData="tabDataIII"
                            :formPropTable="tabPropIII"
                            :tableHeight="tableHeightII"
                        >
                        </CommonTable>
                    </div>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                mainHeight: 716,
                isResize: true,
                isShowNotAllowed:false,
                loadingI:false,
                hanleNo:'',
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                tableHeightI: this.$store.state.screenHei*0.62,
                tableHeightII: 150,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    isMulSelect: true,
                    isInlineForm: true,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    isReadTable: true,
                    isOtherTable: true,
                    isSaving:false,
                },
                showButton:false,
                showUnAudit:false,
                showTable: false,
                tempWidth: null,
                tableData: [],
                //
                searchProp: [
                    {
                        name:'',
                        prop:'refDocCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'clientCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'clientName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    /*{
                        name:'',
                        prop:'docCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },*/
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    }
                ],
                //
                formProp: [
                    {name:'',prop:'refDocCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'deliveryDate',type:'outter'},
                    {name:'',prop:'docStatusName',type:'outter'},
                    {name:'',prop:'isCheckPrice',type:'outter',isCheck:true},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'contractCode',type:'inner'},
                    {name:'',prop:'alPriceMethodName',type:'inner'},
                    {name:'',prop:'alPrice',type:'inner'},
                    {name:'',prop:'companyName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'settlementMethodName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'usrTxt1Name',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //&
                operateProp: [
                    {name:'',prop:'docDate',type:'normal'},
                    {name:'',prop:'sellType',type:'select',selectList:[{value:1,label:''},{value:2,label:''}]},
                    {name:'',prop:'deliveryDate',type:'normal'},
                    {name:'',prop:'docType',type:'normal'},
                    {name:'',prop:'clientName',type:'normal'},
                    {name:'',prop:'deliveryLevel',type:'normal'},
                    {name:'',prop:'contractId',type:'normal'},
                    {name:'',prop:'clientName2',type:'normal'},
                    {name:'PO',prop:'clientPo',type:'normal'},
                    {name:'',prop:'projectName',type:'normal'},
                    {name:'',prop:'remark',type:'normal'},
                    {name:'',prop:'packMethodName',type:'normal'}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'refDocCode',
                        name2:'',
                        prop2:'priceStatus',
                        type:'is_check_price',
                        width: ''
                    },
                    {
                        name1:'/',
                        prop1:'docType',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'enterName',
                        name2:'',
                        prop2:'enterTime',
                        type:'mixin',
                        width: ''
                    }
                ],
                searchForm: {tempStatus: 2,minPriceStatus: 50,maxPriceStatus: 100},
                addForm: {},
                detailForm: {},
                copyForm: {},
                tempForm: {},
                audit:[],
                detailId: null,
                detailIdList: [],
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                tabLeft: '275px',
                activeName: 'first',
                //
                tabPropI: [
                    {prop:'itemNo', name:'',type:'normal',width:130},
                    {prop:'matCode', name:'',type:'normal'},
                    {prop:'cx1Name', name:'',type:'normal'},
                    {prop:'colorDescribe', name:'',type:'normal'},
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'otherPrice',name:'(/m)',width:100,type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'price',name:'(/kg)',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                        ]
                    },
                    {
                        prop:'', name:'(/KG)',
                        children:[
                            {prop:'pricePart1',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'pricePart2',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'pricePart3',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'pricePart4',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'pricePart5',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart1',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart2',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart3',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart4',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart5',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart6',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart7',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                            {prop:'usrPricePart8',name:'',type:'normal',rule:'limitLength',isShort: true,align:'right'},
                        ]
                    },
                    {prop:'machineLevelCode',name:'',type:'normal'},
                    {prop:'usrTxt6Name',name:'',type:'normal'},
                    {
                        prop:'',name:'',
                        children:[
                            {prop:'usrTxt1Name',name:'',type:'normal'},
                            {prop:'usrTxt2Name',name:'',type:'normal'},
                            {prop:'usrTxt3Name',name:'',type:'checkbox',width: 55, align:'center'}
                        ]
                    },
                    {
                        prop:'',name:'',
                        children:[
                            {prop:'matSeriesCode',name:'',type:'normal'},
                            {prop:'matSeriesName',name:'',type:'normal'}

                        ]
                    },
                    {prop:'cx2Name',name:'',type:'normal'},
                    {prop:'cx4Name',name:'',type:'normal',rule:'limitLength',unit:' m',isShort: true,align:'right'},
                    {prop:'cx3Name',name:'',type:'normal',unit:'',isShort: true,align:'right'},
                    {
                        prop:'',name:'',
                        children:[
                            {prop:'usrTxt1',name:''},
                            {prop:'usrTxt2',name:''}
                        ]
                    },
                    {prop:'remark',name:'',type:'normal'}
                ],
                //
                tabDataI: [],
                copyDataI: [],
                tabPropIII: [
                    {
                        prop:'itemNo', name:'',
                    },
                    {prop:'priceItemText', name:''},
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'otherPrice',name:'(/m)'},
                            {prop:'price',name:'(/kg)'},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    }
                ],
                tabDataIII:[]
            }
        },
        mounted() {
            this.getData();
            // /
            this.listenResizeIII();
            this.changeSizeIII();
            this.tempWidth = this.otherVariable.contentWidth;
        },
        methods: {
            //
            getData(isFirst = true) {
                this.tableData = [];
                this.fetch(`/oms/sls/SalesOrderPriceController/getOrderAndContractInfo?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list.length) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.tabDataI = [];
                            this.pageObj.commonTotal = 0;
                            this.$nextTick(() => {
                                this.$refs['tabTableI'].initStatus();
                            })
                        }
                    })
            },
            selectChange(val) {
                if(val === 1) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 101;
                }else if(val === 2) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minPriceStatus = 100;
                    this.searchForm.maxPriceStatus = 100;
                }
            },
            //
            handleRefresh() {
                this.getData();
                this.tabDataI = [];
                this.detailForm = {};
                this.showUnAudit = false;
                this.showButton = false;
            },
            //
            handleReset() {
                this.searchForm = {tempStatus: 2,minPriceStatus: 50,maxPriceStatus: 100};
                this.handleRefresh();
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            //
            changeRefresh() {
                this.getData(false);
                this.otherVariable.isChildEdit = false;
            },
            //
            handleDetail(row) {
                this.isShowNotAllowed = true;
                this.loadingI = true;
                if(row.priceStatus === 0 || row.priceStatus === null || row.priceStatus === 50) {
                    this.showUnAudit = false;
                    this.showButton = true
                }else if(row.priceStatus === 100){
                    this.showUnAudit = true;
                    this.showButton = false
                }else {
                    this.showUnAudit = false;
                    this.showButton = false
                }
                this.detailForm = row;
                this.showTable = false;
                this.refMatPrice = row.alPrice;
                this.tabDataIII = [];
                this.detailId = row.fid;
                this.docStatus = row.docStatus;
                this.docCode = row.docCode;
                this.getDetailTable(row.fid)
            },
            //-
            handleSelectionChange(rows) {
                this.detailIdList = [];
                rows.forEach(item => {
                    this.detailIdList.push(item.fid)
                })
            },
            //
            getDetailTable(fid) {
                this.tabDataI = [];
                this.fetch('/oms/sls/SalesOrderPriceController/getMatOrderAndPrice',{flowId: fid})
                    .then(result => {
                        this.isShowNotAllowed = false;
                        if(result.status === 200 && result.data) {
                            result.data.forEach(item => {
                                item.pricePart8 = this.refMatPrice;
                            });
                            this.tabDataI = result.data;
                        }else {
                            this.tabDataI = [];
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                        this.$refs['tabTableI'].initStatus();
                    })
            },
            //
            handleOtherTable(row) {
                this.tabDataIII = [];
                this.showTable = !this.showTable;
                if(this.showTable) {
                    this.isResize = false;
                    this.tableHeightI = this.tableHeightI - 150;
                    this.showTable = true;
                    this.$nextTick(() => {
                        this.isResize = true;
                    })
                }else {
                    this.tableHeightI = this.tableHeightI + 150;
                }
                //
                this.fetch('/oms/sls/SalesOrderPriceController/getMatPriceItem', {matPriceId:row.priceId})
                    .then(result => {
                        if (result.status === 200) {
                            this.tabDataIII = result.data.list;
                        }else {
                            this.tabDataIII = [];
                        }
                    })
                    .then(() => {
                        if(this.showTable) {
                            this.$refs['tabTableII'].initStatus();
                        }
                    })
            },
            //
            handleAdudit(type) {
                if(this.detailIdList.length>0 && type ==='multi'){
                    this.hanleNo = this.detailIdList.length;

                }else{
                    this.hanleNo='';
                }
                this.$confirm(''+this.hanleNo+'','',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(()=>{
                    let tempObj,api;
                    let isBatch = false;
                    this.otherVariable.isSaving = true;
                    if(type === 'alone') {
                        isBatch = false;
                        tempObj = {orderFlowId: this.detailId, checkPrice: 1};
                        api = '/oms/sls/SalesOrderPriceController/checkPriceInfo';
                    }else {
                        isBatch = true;
                        tempObj = {orderFlowIds: this.detailIdList};
                        api = '/oms/sls/SalesOrderPriceController/checkPriceInfoBatch';
                    }
                    if(isBatch && this.detailIdList.length || !isBatch) {
                        let resultStatus;
                        this.fetch(api,tempObj)
                            .then(result => {
                                resultStatus = result.status;
                                if(result.status === 200) {
                                    this.$message({
                                        message: result.data,
                                        type: 'success'
                                    });
                                    this.getData(false);
                                }else {
                                    this.$message({
                                        message: this.returnErrorMsg(result.error),
                                        type: 'warning'
                                    })
                                }
                            })
                            .then(() => {
                                if(resultStatus === 200) {

                                    this.$refs.commontable.renderRow(this.detailId);
                                }
                                this.otherVariable.isSaving = false;
                            })
                    }else {
                        this.$message({
                            message: '',
                            type:'warning'
                        })
                    }
                })
            },
            //
            handleUnAudit() {
                let tempObj = {
                    orderFlowId: this.detailId,
                    checkPrice: 0
                };
                let resultStatus;
                this.fetch('/oms/sls/SalesOrderPriceController/checkPriceInfo',tempObj)
                    .then(result => {
                        resultStatus = result.status;
                        if(result.status === 200) {
                            this.$message({
                                message: result.data,
                                type: 'success'
                            });
                            this.getData(false);
                        }else {
                            this.$message({
                                message: this.returnErrorMsg(result.error),
                                type: 'warning'
                            })
                        }
                    })
                    .then(() => {
                        if(resultStatus === 200) {
                            this.$refs.commontable.renderRow(this.detailId);
                        }
                    })
            }
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
    .exportCount {
        z-index: 99;
        position: absolute;
        right: 216px;
        bottom: 3px;
        transform: translateY(50%);
        cursor: pointer;
        border-radius: 50%;
        width: 55px;
        height: 55px;
        line-height: 55px;
        text-align: center;
        font-size: 18px;
        color: #fff;
        background: #FF7F00;
    }
    .exportText {
        z-index: 98;
        position: absolute;
        right: 169px;
        bottom: 3px;
        transform: translateY(50%);
        cursor: pointer;
        width: 61px;
        height: 33px;
        line-height: 33px;
        text-align: center;
        font-size: 15px;
        color: #fff;
        background: #FF7F00;
        border-top-right-radius: 16px;
        border-bottom-right-radius: 16px;
    }
    .exportCount1 {
        z-index: 99;
        position: absolute;
        right: 99px;
        bottom: 3px;
        transform: translateY(50%);
        cursor: pointer;
        border-radius: 50%;
        width: 55px;
        height: 55px;
        line-height: 47px;
        text-align: center;
        font-size: 18px;
        color: #666;
        background-color: white;

        border: 1px solid #666;
    }
    .exportText1 {
        z-index: 98;
        position: absolute;
        right: 49px;
        bottom: 3px;
        transform: translateY(50%);
        cursor: pointer;
        /* border-radius: 50%; */
        width: 61px;
        height: 33px;
        line-height: 33px;
        text-align: center;
        font-size: 15px;
        color: #666;
        background-color: white;
        border-top-right-radius: 16px;
        border-bottom-right-radius: 16px;
        border: 1px solid #666;
    }
</style>



